import React from "react";
import './Layout.css';
import LayoutMenu from './menu/LayoutMenu';
import LayoutMenuItem from './menu-item/LayoutMenuItem';
import ReactSVG from 'react-svg';
import {isActive} from "../../utilits/isActive";

class Layout extends React.Component {

    render() {


        return <>

            {this.props.children}

            <footer className="card-footer">
                <div className="container">
                    <div className="rights-mobile">© Все права защищены</div>
                    <LayoutMenu>
                        <LayoutMenuItem href="/" active={isActive('/')}>
                            Фотопроекты
                        </LayoutMenuItem>
                        <LayoutMenuItem href="/past" active={isActive('/past')}>
                            Прошедшие
                        </LayoutMenuItem>
                        <LayoutMenuItem href="/about" active={isActive('/about')}>
                            О нас
                        </LayoutMenuItem>
                        <LayoutMenuItem href="/contact" active={isActive('/contact')}>
                            Обратная связь
                        </LayoutMenuItem >
                    </LayoutMenu>
                    <LayoutMenu>
                        <LayoutMenuItem href="/" active={isActive('/')}>
                            Условия обработки и использования
                            персональных данных
                        </LayoutMenuItem>
                        <LayoutMenuItem href="/past" active={isActive('/past')}>
                            Условия использования сервиса
                        </LayoutMenuItem>
                        <LayoutMenuItem href="/about" active={isActive('/about')}>
                            Условия использования сервиса
                            для организаторов
                        </LayoutMenuItem>
                    </LayoutMenu>
                    <div className="footer-socials menu">
                        <div className="footer-socials-title">
                            Мы в соцсетях
                        </div>
                        <div className="footer-socials-logo">
                            <a className={'vk'} href="https://vk.com">
                                <ReactSVG  src="./images/vk.svg" alt=""/>

                            </a>
                            <a className={'inst'} href="https://www.instagram.com">
                                <ReactSVG  src="./images/inst.svg" alt=""/>
                            </a>
                        </div>
                    </div>
                    <div className={"telephone-and-rights"}>
                        <a className={'telephone'} href="tel:+79876543221"> +7 (987) 654-32-21</a>
                        <div className="rights-desktop">© Все права защищены</div>
                    </div>
                </div>
            </footer>
        </>

    }

}


export default Layout;