import React from 'react';
import LayoutHeader from "../header/LayoutHeader";
import Slider4blocks from '../../slider/Slider4blocks'
const styles=require('./LayoutMain.css');

const slides = [
    './static/media/32064b1aac54770337ec95ea346e2db2.jpg',
    './static/media/3146278.jpg',
    './static/media/IMG_5762_web-1024x684.jpg',
    './static/media/s1200.webp'
];

export default class Layout__Project extends React.Component {
    render() {
        return (<>
                <LayoutHeader isMain/>
                <main className={'main'}>
                    <div className="main__slider-container">
                        <Slider4blocks slides={slides}/>

                    </div>
                    <div className="container">
                        главная
                    </div>
                </main>

            </>
        );
    }


}